
hr {
  color: #000;
  background-color: #000;
  height: 5px;
}
.wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.section {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  width: calc(25% - 42px);
  margin: 21px;
  margin-bottom: 42px;
}
.section .option {
    background-color: #f8f8f8;
    padding: 3px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.section .option.selected {
      background-color: #80ca80;
      color: #f2fbf2;
}
form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}
form input, form a {
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    padding: 7px;
    box-sizing: border-box;
    background-color: #f8f8f8;
    border-radius: 0;
    height: 42px;
}
form input {
    font-size: 21px;
    line-height: 28px;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
