}
footer {
  margin-top: 63px;
  padding: 7px;
  margin-bottom: 0;
  text-align: center;
}
@media (hover: hover) {
.option:hover {
    cursor: pointer;
    background-color: #80ca80;
    color: #f2fbf2;
}
}
@media all and (max-width: 1024px) {
.section {
    width: 100%;
    margin: 0;
}
form {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
form input, form a {
      width: 100%;
      margin: 7px 0;
}
}



